import React from "react";
import { apiRequest } from "../../utils/helper";
import {
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  ComposedChart,
  Area
} from "recharts";
import "./Charts.css";
import { FormControl, TextField, Fab } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
export default function Charts({ date, index, type, ce, pe }) {
  const [chartData, setChartData] = React.useState([]);
  const [chartData2, setChartData2] = React.useState([]);
  const [point, setPoint] = React.useState(21);
  const [point2, setPoint2] = React.useState(21);
  React.useEffect(() => {
    const authHandler = async () => {
      try {
        const userData = await apiRequest({
          url: "https://api.optionalgo.in:6001/v1/getdata",
          method: "post",
          bodyParams: {
            date: date,
            pointtype: type,
            stockname: index,
            strike_price_pe: pe,
            strike_price_ce: ce
          }
        });
        const { data, success } = userData;
        console.log(userData);
        if (success) {
          let cData = [];
          let peak = 0;
          let low = 0;
          let points = point;
          for (let i = 0; i < data.length; i++) {
            let { time_hour_m_in_sec, indexprice, stock } = data[i];

            for (let j = 0; j < stock.length; j++) {
              let data2 = {};

              if (type !== "Both") {
                let {
                  last_price,
                  oi,
                  expiry,
                  strike_price,
                  instrument_type
                } = stock[j];
                data2 = {
                  id: time_hour_m_in_sec,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  last_price: last_price.toFixed(2),
                  oi: oi,
                  expiry: expiry,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else {
                let {
                  last_price,
                  oi,
                  strike_price_pe,
                  strike_price_ce,
                  last_price_ce,
                  last_price_pe,
                  instrument_type
                } = stock[j];
                if (points === point || points === 0) {
                  points = point;
                  peak = last_price.toFixed(2);
                  low = last_price.toFixed(2);
                  points = points - 1;
                } else {
                  if (last_price > peak) {
                    peak = last_price.toFixed(2);
                  }
                  if (last_price < low) {
                    low = last_price.toFixed(2);
                  }
                  points = points - 1;
                }
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  last_price: last_price.toFixed(2),
                  indexprice: indexprice,
                  oi: oi,
                  strike_price_ce: strike_price_ce,
                  strike_price_pe: strike_price_pe,
                  last_price_ce: last_price_ce.toFixed(2),
                  last_price_pe: last_price_pe.toFixed(2),
                  instrument_type: instrument_type,
                  height: [low, peak]
                };
              }

              if (j === 0) {
                console.log(cData);
                cData.push(data2);
              }
            }
          }
          setChartData2(cData);
        }
      } catch (err) {}
    };
    authHandler();
    const interval = setInterval(() => {
      authHandler();
    }, 60000);
    return () => clearInterval(interval);
  }, [date, index, type, ce, pe, point]);

  React.useEffect(() => {
    setChartData([]);
    setChartData2([]);
  }, [date, index, type, ce, pe, point]);

  React.useEffect(() => {
    const updatechart = () => {
      if (chartData.length === 0) {
        setChartData(chartData2);
      } else {
        for (let i = chartData.length; i < chartData2.length; i++) {
          setChartData([...chartData, chartData2[i]]);
        }
      }
    };
    updatechart();
  }, [chartData2, chartData]);

  const customTooltipOnYourLine = e => {
    if (
      e.active &&
      e.payload != null &&
      e.payload[0] != null &&
      type !== "Both"
    ) {
      return (
        <div className="custom-tooltip">
          <p>{e.payload[0].payload["time"]}</p>
          <p>{e.payload[0].payload["last_price"]}</p>
          <p>{e.payload[0].payload["strike_price"]}</p>
          <p>{e.payload[0].payload["indexprice"]}</p>
        </div>
      );
    }
    if (
      e.active &&
      e.payload != null &&
      e.payload[0] != null &&
      type === "Both"
    ) {
      return (
        <div className="customTooltip">
          <div className="tooltipContent">
            <p>LTP: {e.payload[0].payload["last_price"]}</p>
            <p>Puts Price: {e.payload[0].payload["strike_price_pe"]}</p>
            <p>Call Price: {e.payload[0].payload["strike_price_ce"]}</p>
            <p>Puts Last Price: {e.payload[0].payload["last_price_pe"]}</p>
            <p>Call Last Price: {e.payload[0].payload["last_price_ce"]}</p>
            <p>Time: {e.payload[0].payload["time"]}</p>
            <p>Low: {e.payload[0].payload["height"][0]}</p>
            <p>Peak: {e.payload[0].payload["height"][1]}</p>
          </div>
        </div>
      );
    } else {
      return "";
    }
  };

  return (
    <div>
      <ResponsiveContainer width={1000} height={400}>
        <ComposedChart
          width={1000}
          height={400}
          data={chartData}
          margin={{ top: 5, right: 20, bottom: 5, left: 0 }}
        >
          <Line type="monotone" dataKey="last_price" stroke="#8884d8" />
          {point >= 2 ? (
            <Area dataKey="height" stroke="#8884d8" fill="#8884d8" />
          ) : null}
          <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
          <XAxis dataKey="time" />
          <YAxis
            type="number"
            allowDecimals={true}
            allowDataOverflow
            domain={["dataMin-10", "dataMax+10 "]}
          />
          <Tooltip content={customTooltipOnYourLine} />
        </ComposedChart>
      </ResponsiveContainer>
      <div>
        <FormControl variant="outlined">
          <TextField
            id="outlined-basic"
            label="Points"
            variant="outlined"
            defaultValue={21}
            onChange={e => {
              setPoint2(e.target.value);
            }}
          />
          <Fab
            color="primary"
            aria-label="add"
            onClick={() => {
              setPoint(point2);
            }}
          >
            <AddIcon
              onClick={() => {
                setPoint(point2);
              }}
            />
          </Fab>
        </FormControl>
      </div>
    </div>
  );
}
